import { Command, Query } from '@ali/4ever-cangjie';
import { BiPlugin } from '../../types';
import toolbar from './toolbar';
import menubar from './menubar';
import setAlignment from './commands/setAlignment';
import getAlignment from './queries/getAlignment';
import { DEPRECATED_ACTIONS, actions } from './actions';
import onAction from './handlers/onAction';
import onKeyDown from './handlers/onKeyDown';
import sidebar from './sidebar';
import createRenderHoverToolbar from './createRenderHoverToolbar';
import createRenderToolbar from './createRenderToolbar';

const commands: Record<string, Command> = {
  setAlignment,
};

const queries: Record<string, Query> = {
  getAlignment,
};

function createAlignmentPlugin(config): BiPlugin {
  const alignmentConfig = config.alignment || {};
  return {
    toolbar: toolbar(alignmentConfig),
    renderToolbar: createRenderToolbar(alignmentConfig.locale),
    sidebar: sidebar(config),
    menubar: menubar(alignmentConfig),
    commands,
    queries,
    actions,
    onAction,
    onKeyDown,
    models: [],
    renderHoverToolbar: createRenderHoverToolbar(alignmentConfig),
  };
}

createAlignmentPlugin.actions = DEPRECATED_ACTIONS;
createAlignmentPlugin.commands = commands;
createAlignmentPlugin.queries = queries;

export default createAlignmentPlugin;
